* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  border: 2px solid #ddd;
  border-radius: 5px;
  outline: none;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

button[type="submit"], .load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button[type="submit"]:hover, .load-more:hover {
  background-color: #0056b3;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  display: grid;
}

.photo-card {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 8px 8px 0 0;
  display: block;
}

.photo-card:hover img {
  transform: scale(1.1);
}

.info {
  color: #252525;
  background-color: rgba(0, 0, 0, .8);
  padding: 10px;
}

.info p {
  margin: 2px 0;
  font-size: 12px;
}

.info-item b {
  color: #252525;
}

.photo-card .info {
  background-color: #f4f4f4;
  padding: 10px;
}

.photo-card .info .info-item {
  margin-bottom: 5px;
  display: block;
}

.load-more {
  margin: 20px auto;
  display: block;
}

